@use '../../variables' as var;

@mixin flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Participate {
  padding: 120px 15px 0;
  background: #1e1430;
}

.ParticipateDesc {
  max-width: 1134px;
  min-height: 90px;
}

.ParticipateCard {
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 24px;
}

.ParticipateBuy {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  max-width: 1160px;
  min-height: 557px;
  margin: 0 auto;
  color: #fff;
  margin-top: 20px;
  margin-bottom: 199px;

  img {
    width: 480px;
    height: auto;
  }
}

.ParticipateBuyWarp {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}

.ParticipateBuyFormTitle {
  gap: 0;
  h3 {
    font-weight: 700;
    font-size: 36px;
  }
}

.ParticipateBuyFormDesc {
  font-weight: 400;
  font-size: 14px !important;
  color: #ffffff;
  text-align: left !important;
}

.ParticipateBuyImageWarp {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;

  img {
    max-width: 580px;
  }
  // padding: 38px 40px 0 0;
}

.BuyButton {
  flex-direction: column;
  line-height: 18px;
  width: 100%;

  span {
    font-size: 12px;
    color: #ffffff;
    opacity: 0.5;
  }
}

.ParticipateBuyForm {
  max-width: 500px;
  // margin-right: 15px;
  min-width: 45%;
  display: grid;
  gap: 10px;
}

.PaddingWarp {
  display: grid;
  gap: 10px;
  padding: 22px 0px 0px 50px;
}

.Stage,
.Price,
.Cost {
  @include flex;
}

.Cost {
  text-align: right;
  justify-content: flex-end;
}

.StageTag {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 24px 8px 48px 0px;
  min-width: 200px;
  height: 64px;
  color: #ffcd1d;
  font-weight: 700;
  font-size: 20px;
  padding: 0 20px;
  margin-bottom: 30px;

  span {
    font-weight: 700;
    font-size: 16px;
    text-align: right;
    color: #ffffff;
    display: inline-block;
    margin-left: 10px;
  }
}

.StageServings {
  color: #9692a9;
  font-weight: 700;
}

.StageServingsActive {
  color: #ffcd1d;
}

.Attention {
  text-align: left;
  display: grid;
  gap: 10px;
  background: rgba($color: #16122d, $alpha: 0.2);
  border-radius: 12px;
  padding: 18px 22px;
  color: #9644b4;
  display: grid;
  gap: 10px;
  width: 93%;
  margin: 40px auto;
}

.Attention2 {
  text-align: left;
  display: grid;
  gap: 10px;
  background: rgba($color: #16122d, $alpha: 0.2);
  border-radius: 12px;
  padding: 18px 22px;
  color: #9644b4;
  display: grid;
  gap: 10px;
  margin-top: 32px;
}

.AttentionDesc {
  display: grid;
  gap: 10px;
}

.AttentionTitle {
  @include flex;
  img {
    width: 14px;
  }
}

.ThemisGavel {
  @include flex;

  align-items: flex-start;
  max-width: 1160px;
  margin: 50px auto;
  text-align: left;

  img {
    width: 45%;
  }
}

.ThemisGavelTitle {
  text-align: left !important;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0px;
  margin-top: 30px;
}

.ThemisGavelInfoWarp {
  width: 46%;
  margin-left: 10px;
  color: #fff;

  h3 {
    font-size: 36px;
  }

  p {
    font-size: 14px;
  }

  :global {
    .TitleCombo {
      gap: 10px;
    }
  }
}

.BlueText {
  color: #4775fa;
}

.BlueBg {
  background: rgba($color: #3e44cf, $alpha: 0.2);
}

.Invite {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  border-radius: 10px;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 200px;
  position: relative;
  padding: 22px 0 22px 22px;
  color: #fff;
}

.InviteTitle {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 140%;
}

.InviteTextBg {
  position: absolute;
  width: 58%;
  height: auto;
  left: 36px;
  top: 22px;
}

.InviteLeft {
  text-align: left;
}

.InviteRight {
  @include flex;
  justify-content: flex-end;
}

.InviteContentWarp {
  @include flex;
  align-items: flex-start;
  position: relative;
  z-index: 1;
}

.InviteLogo {
  margin: 10px 0;
}

.InviteAwesome {
  width: 124px;
}

.buyButton {
  background: white;
  color: #3e44cf;
  height: 45px;
  padding: 0 20px;
  margin-right: 20px;
}

.buyButtonText {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.ProtocolText {
  background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-left: 10px;
}

.ProgressBarWarp {
  max-width: 1160px;
  margin: 0 auto;
}

.ProgressBar {
  max-width: 100%;
  height: 20px;
  margin: 0 auto;
  background: rgba($color: #2a2e52, $alpha: 0.5);
  border-radius: 10px;
}

.Begins {
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  margin: 45px 0 10px;
}

.ProgressBarActive {
  width: 60%;
  background: linear-gradient(90deg, #bb6ce6 0%, #ffcd1d 100%);
  border-radius: 10px;
  height: 20px;
}

.Rrewards {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 302px;
  height: 47px;
  background: linear-gradient(277.23deg, #1d93cb 0%, #3e44cf 100%);
  border-radius: 23.5px;
  font-size: 20px;
  margin-left: 10px;
  flex: 0 0 302px;

  img {
    margin-right: 10px;
  }
}

.Protocol {
  display: flex;
  flex-direction: column;
  color: #fff !important;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;

  :global {
    .ant-checkbox-wrapper {
      color: #fff;
      margin: 3px 10px;
    }
  }
}

@media (max-width: var.$mobile-break-point) {
  .ParticipateBuy {
    flex-wrap: wrap;
    padding-bottom: 20px;
    margin-bottom: 30px;
  }

  .ParticipateBuyForm {
    max-width: 100%;
    // padding-right: 20px;
    padding: 10px;
  }

  .PaddingWarp {
    padding-left: 20px;
  }

  .ParticipateBuyImageWarp {
    @include flex;

    padding: 0;
    width: 100%;
    justify-content: center !important;
    align-items: center !important;
    margin-top: 30px;

    min-height: auto;

    display: none;

    img {
      width: 100%;
    }
  }

  .StageTag {
    font-size: 14px;

    span {
      display: block;
      font-size: 12px;
    }
  }

  .ThemisGavel {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
    margin-top: 30px;
  }

  .ThemisGavelInfoWarp {
    width: 100%;
    margin-top: 30px;
  }

  .InviteContentWarp {
    flex-wrap: wrap;
  }

  .InviteTitle {
    flex-wrap: wrap;
  }

  .InviteLeft,
  .InviteRight {
    width: 100%;
  }

  .InviteRight {
    justify-content: flex-start;
  }

  .InviteLeft {
    position: relative;
    z-index: 1;
  }

  .InviteAwesome {
    position: absolute;
    top: -20px;
    right: 0;
  }

  .InviteTitleText {
    width: 100%;
  }

  .InviteDesc {
    width: 250px;
    margin: 10px 0;
  }

  .Rrewards {
    margin-left: 0px;
  }

  .ThemisGavelTitle {
    align-items: center;
    h3 {
      font-size: 24px;
      text-align: center;
    }
  }
}
