@use '../../variables' as var;
.Header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var.$header-v-padding var.$header-h-padding;
  z-index: 1;
  background-color: black;
}

.logo {
  display: flex;
  align-items: center;
  img {
    height: 35px;
    margin-right: 15px;
  }
  h2 {
    color: white;
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
  }
}
.menuWrap {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: flex-end;
  gap: 15px;
}
.navWrap {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: flex-end;
  gap: 15px;
  .navItem {
    padding: 10px 15px;
    font-weight: 600;
    font-size: 16px;
    line-height: 12px;
    color: rgba(#fff, 0.5);
    text-decoration: none;
    &.active,
    &:hover {
      color: white;
    }
  }
}
// .menuButton {
//   width: 22px;
// }
.walletButton {
  height: 45px;
}

@media (max-width: var.$mobile-break-point) {
  .logo {
    img {
      height: 30px;
    }
    h2 {
      font-size: 20px;
      line-height: 20px;
    }
  }
  .menuWrap {
    width: 100%;
    position: absolute;
    bottom: -1px;
    left: 0;
    transform: translateY(100%);
    background-color: black;
    flex-direction: column;
    align-items: flex-start;
    padding: calc(var.$header-v-padding * 2) var.$header-h-padding;
  }
  .navWrap {
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    .navItem {
      padding: 20px;
      width: 100%;
      border-radius: 8px;
      text-align: left;
      color: white;
      &.active,
      &:hover {
        background: rgba(62, 68, 207, 0.2);
        color: #3e44cf;
      }
    }
  }
  .walletButton {
    height: 40px;
  }
}
