@mixin flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.NumberInput {
  @include flex;
  user-select: none;
}

.NumberButton {
  @include flex;
  justify-content: center;
  width: 72px;
  height: 72px;
  background: #1e1a30;
  cursor: pointer;

  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.8;
  }
}

.NumberButtonLeft {
  border-radius: 12px 0px 0px 12px;
}

.NumberButtonRight {
  border-radius: 0px 12px 12px 0px;
}

.NumberInputValue {
  @include flex;
  justify-content: center;
  background: #0e0c16;
  width: 100%;
  height: 72px;
  color: #ffcd1d;
  font-weight: 700;
  font-size: 20px;
}
