@use '../../variables' as var;

.LearnMore {
  padding: 120px 0;
  background: #1e1430;
  background: #000;
}

.EcosystemTitleSec {
  margin-top: 100px;
  h2 {
    font-size: 48px;
  }
}

.EcosystemMap {
  img {
    max-width: 1140px;
  }
}

.CorePhilosophWarp {
  display: flex;
  justify-content: space-between;
  max-width: 1149px;
  margin: 0 auto;
  margin-top: 60px;
}

.CorePhilosophItem {
  color: #fff;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 30px;
  gap: 40px;
  max-width: 700px;
  background: #181128;
  border-radius: 30px;
  margin: 0 20px;
}

.CorePhilosophItemContentWarp {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}

.CorePhilosophItemTitle {
  font-weight: 600;
  font-size: 24px;
  color: #ffffff;
  margin-bottom: 37px;
}

.CorePhilosophItemContent {
  color: rgba($color: #ffffff, $alpha: 0.5);
}

.EcosystemMap {
  background: #1e1430;
  padding: 46px 10px 60px;
  margin-top: 120px;

  img {
    width: 100%;
  }
}

.Infrastructure {
  background: #26153f;
  padding: 80px 10px;
}

.InfrastructureDesc {
  color: #fff;
  text-align: left;
  max-width: 1150px;
  margin: 0 auto;
}

.InfrastructureWarp {
  display: flex;
  align-items: flex-start;
  max-width: 1150px;
  margin: 0 auto;
}

.InfrastructureItem {
  color: #fff;
  width: 24%;

  img {
    width: 100%;
  }
}

.InfrastructureItemTitle {
  margin-top: 20px;
  h2 {
    font-size: 20px;
    min-height: 80px;
  }
}

.InfrastructureItemContent {
  font-size: 14px;
  color: rgba($color: #ffffff, $alpha: 0.5);
}

@media (max-width: 1199px) {
  .InfrastructureWarp {
    flex-wrap: wrap;
  }

  .InfrastructureItem {
    width: 50%;
  }
}

@media (max-width: var.$mobile-break-point) {
  .CorePhilosophWarp {
    flex-wrap: wrap;
  }

  .CorePhilosophItem {
    width: 100%;
    margin-bottom: 20px;
  }

  .InfrastructureWarp {
    flex-wrap: wrap;
  }

  .InfrastructureItem {
    width: 100%;
  }
}
