@use '../../../variables' as var;
.TitleCombo {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 600;
    font-size: 70px;
    line-height: 110%;
    background: var.$color-title-gradient;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
  }
  &.small {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-weight: 600;
      font-size: 40px;
    }
    p {
      font-weight: 400;
      font-size: 16px;
    }
  }
}
@media (max-width: var.$mobile-break-point) {
  .TitleCombo,
  .TitleCombo.small {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-size: 24px;
    }
    p {
      font-size: 16px;
    }
  }
}
