@use '../../variables' as var;
.About {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.banner {
  height: 100vh;
  width: 100%;
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center bottom;
  }
}
.bannerTextWrap {
  position: absolute;
  top: 5vh;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
  max-width: 1000px;
}
.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
  padding-bottom: 0;
}
.mainContent {
  width: 100%;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  &::after {
    content: 'placeholder';
    visibility: hidden;
  }
}

.getNodeWrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  .buttonWrap {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 15px;
    .getNftButton {
      flex: 1;
      width: 100%;
      max-width: 180px;
    }
  }
}
.getNodeCombo {
  max-width: 900px;
}
.statsWrap {
  width: 100%;
}
.statsItem {
  width: 100%;
  // padding: 40px 0;
  border: 2px solid #545454;
  background-color: rgba($color: #000000, $alpha: 0.3);
  border-radius: 10px;
  display: flex;
  overflow: hidden;
}
.statsIconWrap {
  border-right: 2px solid #545454;
  background: #08021f;
  img {
    width: 45px;
    margin: 20px;
  }
}
.statsTextWrap {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  h2 {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    /* identical to box height, or 18px */
    text-align: center;
  }
  p {
    font-weight: 600;
    font-size: 28px;
    line-height: 100%;
    text-align: center;
    color: #e7e7e7;
  }
}
.nftIntro {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.nftIntroItem {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 50px;
  video {
    border-radius: 10px;
    width: 40%;
  }
  h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 100%;
    /* identical to box height, or 24px */
    color: #e7e7e7;
  }
  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
  }
  &.second {
    justify-content: flex-end;
  }
}
.nftIntroAction {
  width: 40%;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  gap: 25px;

  &.second {
    text-align: right;
    align-items: flex-end;
  }
}

.pointsSystemWrap {
  width: 100%;
  height: 70vh;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pointsSystemContainer {
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
.saleWrap {
  margin-bottom: 50px;
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 10px;
  padding: 50px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  overflow: hidden;
}
.saleTextWrap {
  display: flex;
  align-items: center;
  gap: 15px;

  img {
    height: 40px;
    width: auto;
  }
  .vbar {
    border: none;
    border-right: 1px solid rgba(white, 0.8);
    height: 40px;
  }
}
.saleWatermark {
  position: absolute;
  height: 80%;
  top: 50%;
  left: 15%;
  transform: translate(0, -50%);
}

.buyButton {
  background: white;
  color: #3e44cf;
  height: 45px;
  padding: 0 20px;
}

@media (max-width: var.$mobile-break-point) {
  .bannerTextWrap {
    top: 10vh;
  }
  .nftIntroItem {
    // flex-wrap: wrap;
    gap: 25px;
    flex-direction: column;
    .nftIntroAction,
    video {
      width: 100%;
      max-width: 500px;
    }
    h2 {
      font-size: 16px;
    }
    p {
      font-size: 14px;
    }
    &.second {
      flex-direction: column-reverse;
    }
  }
  .nftIntroAction,
  .nftIntroAction.second {
    align-items: center;
    text-align: center;
  }
  .saleWrap {
    flex-direction: column;
    padding: 30px 20px;
  }
  .saleTextWrap {
    flex-direction: column;
    width: 100%;
    gap: 25px;
    img {
      height: 35px;
    }
    .vbar {
      border: none;
      // border-right: 1px solid rgba(white, 0.8);
      border-bottom: 1px solid rgba(white, 0.8);
      width: 100%;
      height: auto;
    }
    .saleText {
      width: 90%;
      height: auto;
      max-width: 300px;
    }
  }
  .saleWatermark {
    height: 75%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
